<template><section><html><head></head><body><h1>导航罗盘</h1>
<p><code v-pre="">vc-navigation-sm</code> 组件用于加载导航罗盘部件——超图样式。<strong>注意：</strong> 使用此组件需要引入 <code v-pre="">import 'vue-cesium/lib/vc-navigation-sm.css'</code>，需要<code v-pre="">css-loader</code>。</p>
<h2>示例</h2>
<h3>加载导航罗盘部件</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer :timeline="timeline" @ready="ready">
        <vc-navigation-sm :options="options"></vc-navigation-sm>
      </vc-viewer>
      <div class="demo-tool">
        <span>罗盘部件</span>
        <md-switch v-model="options.enableCompass"></md-switch>
        <span>缩放部件</span>
        <md-switch v-model="options.enableZoomControl"></md-switch>
        <span>罗盘外环是否可用</span>
        <md-switch v-model="options.enableCompassOuterRing"></md-switch>
      </div>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-navigation</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"navigation"</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"options"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-navigation</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-tool"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>罗盘部件<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"options.enableCompass"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">md-switch</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>缩放部件<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"options.enableZoomControls"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">md-switch</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>比例尺部件<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"options.enableDistanceLegend"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">md-switch</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>罗盘外环是否可用<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"options.enableCompassOuterRing"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">md-switch</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">options</span>: {
          <span class="hljs-comment">// 用于启用或禁用罗盘。true是启用罗盘，false是禁用罗盘。默认值为true。如果将选项设置为false，则罗盘将不会添加到地图中。</span>
          <span class="hljs-attr">enableCompass</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-comment">// 用于启用或禁用缩放控件。true是启用，false是禁用。默认值为true。如果将选项设置为false，则缩放控件将不会添加到地图中。</span>
          <span class="hljs-attr">enableZoomControls</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-comment">// 用于启用或禁用距离图例。true是启用，false是禁用。默认值为true。如果将选项设置为false，距离图例将不会添加到地图中。</span>
          <span class="hljs-attr">enableDistanceLegend</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-comment">// 用于启用或禁用指南针外环。true是启用，false是禁用。默认值为true。如果将选项设置为false，则该环将可见但无效。</span>
          <span class="hljs-attr">enableCompassOuterRing</span>: <span class="hljs-literal">true</span>
        }
      }
    },
    <span class="hljs-attr">methods</span>: {
      <span class="hljs-title function_">ready</span>(<span class="hljs-params">cesiumInstance</span>) {
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">cesiumInstance</span> = cesiumInstance
        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = <span class="hljs-variable language_">this</span>.<span class="hljs-property">cesiumInstance</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>defaultResetView</td>
<td>Object</td>
<td>中国</td>
<td><code v-pre="">optional</code> 指定重置按钮的位置。<strong>结构：{lng: number, lat: number, height: number} or {west: number,south: number,east: number,north: number}</strong></td>
</tr>
<tr>
<td>enableCompass</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre="">optional</code> 指定是否启用罗盘部件。</td>
</tr>
<tr>
<td>enableZoomControls</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre="">optional</code> 指定是否启用缩放部件。</td>
</tr>
<tr>
<td>enableDistanceLegend</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre="">optional</code> 指定是否启用距离比例尺部件。</td>
</tr>
<tr>
<td>enableCompassOuterRing</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre="">optional</code> 指定罗盘部件是否可用。</td>
</tr>
</tbody>
</table>
<hr>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例。</td>
</tr>
</tbody>
</table>
</body></html></section></template>

<script>
    export default {
      data () {
        return {
          timeline: true,
          options: {
            enableCompass: true,
            enableZoomControl: true,
            enableCompassOuterRing: true
          }
        }
      },
      methods: {
        ready (cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          const {Cesium, viewer} = this.cesiumInstance
        }
      }
    }
  </script>